.about-restaurant__container {
  position: relative;
  padding-top: 16px;
  // padding-bottom: 72px;
  @include tablet {
    // padding-top: 120px;
    padding-bottom: 262px;
  }
  @include desktop {
    padding-bottom: 15px;
  }
}

.about-restaurant__subtitle {
  // шрифт
  font-family: 'Times New Roman', serif;
  font-size: 20px;
  line-height: 1.15;
  color: $mainDarkColor;

  margin-bottom: 12px;

  @include tablet {
    font-size: 32px;
    line-height: 1.16;
    margin-bottom: 4px;
  }
}

.about-restaurant__logo {
  // шрифт
  font-family: 'Times New Roman', serif;
  font-weight: 700;
  font-size: 40px;
  line-height: 1.08;
  letter-spacing: 0.3em;
  text-transform: uppercase;
  color: $secondaryAccentColor;

  margin-bottom: 12px;

  @include tablet {
    font-size: 64px;
    margin-bottom: 24px;
  }
  @include desktop {
    color: $secondaryAccentColor;
  }
}

.info-cards {
  @media screen and (max-width: 1023px) {
    margin-bottom: 32px;
  }
}

.info-card {
  @include tablet {
    display: flex;
  }
}

.info-card:first-child {
  margin-bottom: 32px;
  @include tablet {
    margin-bottom: 142px;
  }
  @include desktop {
    margin-bottom: 162px;
  }
}

.info-card:nth-child(2n):not(:last-child) {
  margin-bottom: 32px;
  @include tablet {
    margin-bottom: 0;
  }
}

.info-card__description {
  font-family: 'Times New Roman', sans-serif;
    font-weight: 400;
    font-size: 20px;
  // line-height: 1.25;
  // letter-spacing: 0.02em;
  @include tablet {
    // font-size: 16px;
  }
}

.info-card:first-child .info-card__description {
  // шрифт
  color: $primaryTextColor;

  // max-width: 258px;
  margin-bottom: 28px;

  @include tablet {
    max-width: 539px;
    margin-bottom: 0;
  }
}

.info-card:first-child .info-card__thumb {
  margin-left: auto;
  width: 244px;
  @include tablet {
    position: absolute;
    top: -32px;
    right: 65px;
    margin-left: 0;
    width: 400px;
  }
  @include desktop {
    top: -33px;
    right: 95px;
    width: 460px;
  }
}

.info-card__thumb {
  box-shadow: 2px 2px 60px 2px $primaryShadowColor;
  border-radius: 2px;
  overflow: hidden;
  @include tablet {
    box-shadow: 5px 5px 70px 17px $primaryShadowColor;
  }
}

.info-card:not(:first-child)  .info-card__thumb{
  @include desktop {
    box-shadow: none;
  }
}

.info-card:nth-child(n + 2) .info-card__description {
  color: $secondaryAccentColor;
  max-width: 163px;
  @include tablet {
    max-width: 312px;
  }
    @include desktop {
      max-width: 412px;
    }
}

.info-card:nth-child(n + 2) {
  display: flex;
  align-items: center;
}

.info-card:nth-child(2n) {
  flex-direction: row-reverse;
  justify-content: flex-end;
}

.info-card:nth-child(2n) .info-card__description {
  margin-left: 20px;
  @include tablet {
    margin-left: 48px;
  }
  @include desktop {
    margin-left: 92px;
  }
}

.info-card:nth-child(2n + 3) .info-card__description {
  margin-right: 20px;
  @include tablet {
    margin-right: 48px;
  }
  @include desktop {
    margin-right: 32px;
  }
}

.info-card:nth-child(n + 2) .info-card__thumb {
  width: 200px;
  @include tablet {
    width: 350px;
  }
  @include desktop {
    width: 460px;
  }
}

.slider-about__item {
  border-radius: 2px;
  overflow: hidden;
}
