.advantages {
    text-align: center;
    // padding: 60px 0 60px;
    background-color: $primaryBgColor;
    position: relative;
    margin: 0 auto;
    padding: 0;

    @include mobile {
        // width: 480px;
    }

    @include tablet {

        padding: 0 0 20px;
        // width: 768px;
    }

    @include desktop {
        // width: 1200px;
        height: 100%;
    }
}

.advantages__title .products__title {
    // visibility: hidden;
    position: absolute;
}

.advantages__list {
    @include tablet {
        display: flex;
        justify-content: center;

        &:not(:last-child) {
            margin-right: 60px;
        }
    }
}

.advantages__item {
    margin-bottom: 32px;

    @include tablet {
        margin-bottom: 0;

        &:not(:last-child) {
            margin-right: 80px;
        }
    }

    @include desktop {
        &:not(:last-child) {
            margin-right: 32px;
        }
    }
}

.advantages__thumb-cow-icon::before {
    content: " ";
    display: inline-block;
    width: 50px;
    height: 50px;
    background-image: url(/src/images/weight.png);
    background-repeat: no-repeat;
    background-size: contain;
    margin-bottom: 16px;

    @include tablet {
        margin-bottom: 32px;
    }

    @include desktop {
        margin-bottom: 40px;
    }
}

