.advantages__number {
    margin-bottom: 16px;
    font-weight: 700;
    font-size: 36px;
    line-height: 1.22;
    letter-spacing: 0.04em;
    color: $secondaryAccentColor;

    @include tablet {
        margin-bottom: 12px;
    }

    @include desktop {
        margin-bottom: 12px;
    }
}

.advantages__preTitle {
    display: inline-flex;
    font-weight: 700;
    font-size: 20px;
    line-height: 1.66;
    color: $mainDarkColor;
    width: 300px;
    text-align: center;
    align-items: center;

    @include tablet {
        width: 152px;
        font-weight: 700;
        font-size: 18px;
        line-height: 1.71;
    }

    @include desktop {
        width: 368px;
        font-weight: 700;
        font-size: 26px;
        line-height: 1.75;
        text-align: center;
    }
}