/* Стили для модальных окон */
.modal {
  /*  Установаем ширину окна  */
  width: 100%;
  padding: 60 15px;
  box-shadow: $modalShadow;
  border-radius: 5px;
  @include mobile {
    width: 414px;
    padding: 60px 15px;
  }
  @include tablet {
    width: 712px;
    padding: 60px;
  }
  

  /*  Центрируем и задаем z-index */
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1);
  z-index: 30; /* Должен быть выше чем у подложки*/
  
  /*  Побочные стили   */
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12),
  0px 1px 1px rgba(0, 0, 0, 0.14),
  0px 2px 1px rgba(0, 0, 0, 0.2); 
  text-align: center;
  
  border-radius: 5px;
  background-color: $primaryBgColor;
  transition: transform $duration $timingFunction;

  /* Скрываем окна  */
  opacity: 0;
  visibility: hidden;
}


/* Стили для активных классов подложки и окна */

.modal.active,
.overlay.active {
  opacity: 1;
  visibility: visible;
}


/* Стили для кнопки закрытия */

.modal__close {
  display: block;
  background-color: transparent;
  border: none;
  position: absolute;
  right: 16px;
  top: 16px;
  margin-left: auto;
  margin-right: auto;
  fill: $mainDarkColor;
  cursor: pointer;
  &:hover,
  &:focus {
    fill: $accentColor;
  }
}


.modal-form {
  display: flex;
  flex-direction: column;
  @include tablet {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}


.modal__title {
  font-family: 'Times New Roman';
  font-style: normal;
  font-weight: bold;
  font-size: 35px;
  line-height: calc(28 / 24);
  letter-spacing: 0.05em;
  margin-bottom: 40px;
  color: $mainDarkColor;
  text-align: center;
  @include tablet {
    font-size: 40px;
    line-height: calc(46/40);
  }
}
.modal-input {
  width: 100%;
  height: 54px;
  border: 1px solid $secondaryAccentColor;
  border-radius: 2px;
  padding-left: 12px;
  outline: transparent;
  cursor: pointer;
  &:focus {
    border-color: $secondaryAccentColor;
  }
  font-family: 'Times New Roman';
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 23px;
  letter-spacing: 0.05em;
  color: $modalPlaceholderColor;
  background-color: $primaryBgColor;
}

input[type='date'] {
  background-image: url('../images/arrow-down.svg');
  background-repeat: no-repeat, repeat;
  background-position: right 0.7em top 50%, 0 0;
  background-size: 24px auto, 100%;
}
input[type='date']::before {
  content: attr(placeholder);
  width: 100%;
}
input[type='date']:focus::before,
input[type='date']:valid::before {
  display: none;
}

.modal-input::-webkit-inner-spin-button,
.modal-input::-webkit-calendar-picker-indicator {
  display: none;
  -moz-appearance: none;
  -webkit-appearance: none;
}


.modal-wrap {
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  @include tablet {
    width: 288px;
  }
}
.modal-wrap-room {
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  @include tablet {
    margin-left: 304px;
    width: 288px;
  }
}
.modal-wrap-room-price {
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  @include tablet {
  margin-right: 304px;
    width: 288px;
  }
}
.user-name-text {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: calc(17 / 14);
  letter-spacing: 0.02em;
  color: $secondaryAccentColor;
  padding-left: 12px;
  margin-top: 8px;
  text-align: start;
}
.button-submit {
  @extend .button-modal;
  margin-top: 20px;
}
.user-name-text-date {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: calc(20 / 16);
  letter-spacing: 0.02em;
  text-align: start;
  color: $secondaryAccentColor;
  padding-left: 12px;
  margin-top: 8px;
}

/* Стили для подложки */

.overlay {
  
  /* Скрываем подложку  */
  opacity: 0;
  visibility: hidden;
  
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  z-index: 20;
  transition: .3s all;
}

.overlay .modal.active {
  pointer-events: none;
}

.socials-modal{
  display: inline-block;
  @include tablet {
      display: flex;
    }
}

.soc__icon {
margin-bottom: 20px;
width: 150px;
height: 100px;
  fill: rgb(92, 165, 224);
}