.products__label {
    
    font-weight: 400;
    font-size: 18px;
    line-height: 1.22;
    text-transform: uppercase;

    @media screen and (min-width:768px) {
        font-size: 22px;
    }

    @media screen and (min-width:1200px) {
        font-size: 30px;
    }
}

.products__title {
    margin-bottom: 10px;
    padding-top: 30px;
    font-size: 30px;
    font-family: 'Times New Roman', sans-serif;
    color: $mainDarkColor;
    font-weight: 700;
    line-height: 1.3;
    letter-spacing: 0.06em;
    text-transform: uppercase;

    @media screen and (min-width:768px) {
        margin-bottom: 10px;
        font-size: 34px;
    }

    @media screen and (min-width:1200px) {
        margin-bottom: 10px;
        font-size: 36px;
    }
}

.products__list {
    // padding-top: 108px;
    // width: 280px;
    margin-right: auto;
    margin-left: auto;

    @media screen and (min-width:480px) {
        width: 250px;
    }

    @media screen and (min-width:768px) {
        display: flex;
        // padding-top: 120px;
        width: 704px;
    }

    @media screen and (min-width:1200px) {
        // padding-top: 183px;
        width: 1168px;
    }
}

.products__item {
    position: relative;
    min-height: 420px;
    width: 100%;
    padding: 12px 20px 16px;
    display: flex;
    flex-direction: column;

    box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
    transition: box-shadow 250ms linear;
    border-radius: 24px;

    &:hover,
    &:focus {
        box-shadow: 0px 8px 30px rgba(0, 0, 0, 0.25), 0px 8px 30px rgba(0, 0, 0, 0.25);
    }

    @media screen and (min-width:480px) {
        min-height: 428px;
        padding-top: 15px;
        padding-left: 25px;
        padding-right: 25px;
        width: 100%;
    }

    @media screen and (min-width:768px) {
        padding: 15px 26px 12px;
        width: calc((100% - 32px) / 3);
    }

    @media screen and (min-width:1200px) {
        padding: 15px 20px 60px;
        min-height: 536px;
        width: calc((100% - 58px) / 3);
    }
}

.products__item:not(:last-child) {
    margin-bottom: 20px;

    @media screen and (min-width:768px) {
        margin-right: 16px;
        margin-bottom: 0;
    }

    @media screen and (min-width:1200px) {
        margin-right: 29px;
    }
}

