.products__item:nth-child(1) {
    
    background-color: $secondaryTitleColor;
    transition: color $duration $timingFunction,
            background-color $duration $timingFunction;
            color: $secondaryAccentColor;
}

.products__item:nth-child(2) {
    background-color: $secondaryTitleColor;
    transition: color $duration $timingFunction,
            background-color $duration $timingFunction;
            color: $secondaryAccentColor;
}

.products__item:nth-child(3) {
    background-color: $secondaryTitleColor;
    transition: color $duration $timingFunction,
            background-color $duration $timingFunction;
            color: $secondaryAccentColor;
}

