.products__subtitle {
    margin-bottom: 10px;
    margin-top:20px;
    font-size: 20px;
    line-height: 1.23;
    text-transform: uppercase;
    color: $accentColor;

    @media screen and (min-width:768px) {
        font-size: 18px;
    }

    @media screen and (min-width:1200px) {
        margin-bottom: 15px;
        font-size: 30px;
    }
}

.products__text {
    font-size: 18px;
    line-height: 1.76;
    text-align: left;
    color: $mainLightColor;

    @media screen and (min-width:480px) {
            font-size: 14px;
        }

    @media screen and (min-width:1200px) {
        font-size: 24px;
    }
}

.products__text_2 {
    font-size: 20px;
    line-height: 1.76;
    font-weight: 700;
    color: $mainLightColor;
    margin-bottom: 10px;

    @media screen and (min-width:1200px) {
        font-size: 24px;
    }
}

.products__btn {
    margin-bottom: 0;
    margin-top: auto;
    border: none;
    background-color: transparent;
    text-align: center;
    color: $mainLightColor;

    &:active {
        color: $accentColor;
    }

    &:hover .products__icon,
    &:focus .products__icon {
        scale: 1.5;
    }

        @media screen and (min-width:1200px) {
            margin-top: auto;
        }
}

.products__icon {
    scale: 1;
    transition: scale 250ms linear;
    fill: $mainLightColor;
    // background-color: $mainLightColor;
}