@mixin mobile {
  @media screen and (min-width: 414px) {
    @content;
  }
}
@mixin tablet {
  @media screen and (min-width: 1024px) {
    @content;
  }
}
@mixin desktop {
  @media screen and (min-width: 1280px) {
    @content;
  }
}
@mixin retina {
  @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    @content;
  }
}